body {
  font-family: 'montserrat-regular', Helvetica, Arial, sans-serif;
  font-size: 13px;
  @include font-smoothing;
  background: #FFF;
  color: #FFF; }

.out {
  overflow: hidden;
  color: black; }

.h1 {
  @include f(pr, 50, 60);
  line-height: 1.4; }

.h2 {
  @include f(b, 50, 60);
  line-height: 1.4;
  text-transform: uppercase; }

.h3 {
  @include f(b, 25, 120);
  text-transform: uppercase; }

.h4 {
  @include f(pi, 16, 120);
  color: #919191;
  text-transform: uppercase; }

@include r(767) {
  .h1 {
    font-size: 32px; }
  .h2 {
    font-size: 32px; } }

.svg-icon {
  display: inline-block;
  font-size: 40px;
  svg {
    display: block;
    width: 100%;
    height: 100%; } }

.btn {
  display: inline-block;
  background: none;
  box-sizing: border-box;
  text-transform: uppercase;
  color: inherit;
  text-align: center;
  position: relative;
  z-index: 0;
  box-sizing: border-box;
  text-decoration: none;
  width: 240px;
  height: 70px;
  line-height: 70px;
  @include f(r, 16, 250);
  &_light {
    color: #FFF;
    .btn__border {
      > span {
        background: rgba(white, 0.4);
        &:before {
          background: white; } } } }
  &_font-sm {
    font-size: 14px; }
  &:hover {
    .btn__border {
      > span {
        &:before {
          transform: translate3d(0,0,0); } } } } }

.btn__text {
  position: relative;
  z-index: 1; }

.btn__border {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  > span {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(black, 0.4);
    overflow: hidden;
    &:before {
      content: "";
      display: block;
      background: black;
      position: absolute;
      transition: transform 0.5s ease;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; } }
  .top {
    height: 1px;
    bottom: auto;
    right: 1px; }
  .bottom {
    height: 1px;
    top: auto;
    left: 1px; }
  .left {
    top: 1px;
    width: 1px;
    right: auto; }
  .right {
    width: 1px;
    left: auto;
    bottom: 1px; }
  .top {
    &:before {
      transform: translate3d(-100%,0,0); } }
  .bottom {
    &:before {
      transform: translate3d(100%,0,0); } }
  .right {
    &:before {
      transform: translate3d(0,-100%,0); } }
  .left {
    &:before {
      transform: translate3d(0,100%,0); } } }
